export default {
	name:'scoreStar',
	data(){
		return{
			yelloScore:0,
			grayScore:5
		}
	},
	props:{
		innerScore:0,
		showNum:0
	},
	created(){
		
		if(this.innerScore!=null && this.innerScore!=undefined &&this.innerScore!=''){
			this.yelloScore = parseInt(this.innerScore/2)
			this.grayScore = 5  - this.yelloScore		
		}

	}
}
